//! 配色方案

// 背景颜色
$background-color-page: #fff;
$background-color-head: #6699cc;
$background-color-slogan: #ffffb3;

// 文本颜色
$text-color-body: #454545;
$text-color-header: #fff;
$text-color-emphasized: #f471da;
$text-color-slogan-p: #7b5e37;
$text-color-slogan: #8080ff;


//! 字体设置

$font-family-body: Arial, Helvetica, sans-serif, "Microsoft YaHei UI", "Microsoft YaHei",;
$font-family-slogan: Arial, Helvetica, sans-serif, "Microsoft YaHei UI", "Microsoft YaHei",;

$font-size-body: 1rem;
$font-size-heading: $font-size-body * 1.2;
$font-size-footer: $font-size-body * 0.9;

$letter-spacing-en: 0.1em;
$letter-spacing-cn: 0.2em;
$letter-spacing-extra: 0.3em;

$line-height-header: 3em;
$line-height-footer: 1.4em;
$line-height-default: 1.5em;
$line-height-content: 2em;

//! 布局

$height-header: 4em;
$height-slogan-square: 80vw;